html,
body {
  margin: 0;
  padding: 0;
  background-attachment: fixed;
  font-family: "Roboto", sans-serif;
}
nav {
  text-align: center;
}
nav a {
  color: #fff;
  text-decoration: none;
  font-weight: 600;
}
.under_construction {
  width: 50vw;
}

* {
  box-sizing: border-box;
}

.fullscreen-bg {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  z-index: -100;
}

.fullscreen-bg video {
  position: absolute;
  top: 50%;
  left: 50%;
  width: auto;
  height: auto;
  min-width: 100%;
  min-height: 100%;
  transform: translate(-50%, -50%);
}

